.nav {
  &_list {
    list-style: none;
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }

  &_link {
    border: none;
    color: black;
    text-decoration: none;

    &.active {
      border-bottom: 2px solid #000;
    }
  }
}